/* Card */
.movie-card {
    background: rgba(255, 255, 255, 0.3) !important;
    border: 0.5px solid rgba(255, 255, 255, 0.5);
    box-sizing: border-box;
    box-shadow: 0px 50px 100px rgba(0, 0, 0, 0.25);
    backdrop-filter: blur(40px);
    border-radius: 0px 0px 0px 0px;
}

.open-button {
    width: 100%;
}